.history {
    padding-left: 20px;
    padding-right: 20px;
    background-color: $grey;
    @media (min-width: $tablet-width) {
        padding-left: 30px;
    padding-right: 30px;
    background-color: #ffffff;
       // background-color: white;
    }
    @media (min-width: $desktop-width) {
        margin-top: 26px;
        padding-left: 110px;
    padding-right: 110px;
    }
}

.history__wrapper {
    
    margin-left: auto;
    margin-right: auto;
    width: 100%;

    outline: 5px solid red;
    outline-offset: -5px;
    @media (min-width: $tablet-width) {   
        
    }
    @media (min-width: $desktop-width) {           
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 64px;
        

    }   
}

.history__title {
    margin-top: 53px;
    margin-bottom: 40px;
    padding-top: 25px;
    text-align: start;
    font-family: Oswald;
    font-size: 36px;
    font-style: normal;
    font-weight: 400;
    line-height: 40px; 
    @media (min-width: $tablet-width) {   
        margin-top: 0;
        padding-top: 0;
        font-size: 60px;
        line-height: 60px;
        text-align: start;
        margin-bottom: 63px;
        font-size: 60px;
        font-style: normal;
        font-weight: 400;
        line-height: 60px;
    }
    @media (min-width: $desktop-width) {   
        font-size: 60px;
        line-height: 60px;
        margin-bottom: 76px;  
        margin-top: 43px;     
    }   
}


//    
.history__div {
    @media (min-width: $tablet-width) {
        display: flex;
        width: 100%;
        align-content: space-between;
        margin-bottom: 80px;

        outline: 5px solid red;
        outline-offset: -5px;
    }
    @media (min-width: $desktop-width) {
        display: flex;
        flex-direction: column;

    }


}

.history__text {
    margin-bottom: 20px;
    color: var(--special_dark-grey, #444);
    font-family: Arial;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    @media (min-width: $tablet-width) {   
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 54px;
    }
    @media (min-width: $desktop-width) {   
        font-size: 16px;
        line-height: 24px;
        min-width: 436px;
        margin-right: 194px;
        margin-bottom: 69px;
        
    }   
}

.history__table {
    display: grid;
    grid-template-columns: auto auto;
    gap: 32px;
    outline-style: none;
    @media (min-width: $tablet-width) {   
        gap: 42px;
}
    @media (min-width: $desktop-width) {  
        display: flex;
        min-width: 366px; 
        margin-top: 55px;
        
    }   
}

.history__list {
    display: flex;
    flex-direction: column;
   position: relative;
   margin-top: 0;
   min-width: 124px;
    margin-bottom: 20px;
    
    @media (min-width: $tablet-width) {
        width: 162px;
        height: 72px;
    }
    @media (min-width: $desktop-width) {   
        margin-right: 0;
        
    }   
}
    

.history__item {
    position: relative;
    z-index: 0;
    margin-bottom: 8px;
    text-align: center;
    font-family: Oswald;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px; 

    border: 3px solid #CDCDCD;
    padding-bottom: 18px;
    padding-top: 14px;
    @media (min-width: $tablet-width) {   
        font-size: 30px;
        line-height: 37px;
        padding-bottom: 20px;
    padding-top: 15px;
    }
    @media (min-width: $desktop-width) {   
        
        
    }   
}

.history__description {
    position: absolute;
    top: 46px;
    z-index: 2;
    max-width: 77px;
    margin-bottom: 20px;
    background-color: #EAEAEA;
    
    align-self: center;
    color: var(--special_dark-grey, #444);    
    margin-left: 0;
    text-align: center;
    font-family: Arial;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px; /* 100% */

    @media (min-width: $tablet-width) {   
        top: 63px;
        background-color: #ffffff;
    }
    @media (min-width: $desktop-width) {   
        
        
    }   
}

.history__spent {
    display: block;
    margin-bottom: 40px;
    color: #000;
    text-align: center;
    font-family: Oswald;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 142.857% */
    text-transform: uppercase;

    @media (min-width: $tablet-width) {  
        display: flex;
        width: 175px;
        flex-wrap: wrap;
        text-align: right;
        align-items: center;
        margin-left: auto;
        margin-right: 0;
        font-size: 20px;
        line-height: 20px; /* 100% */
        text-transform: uppercase;
       margin-top: 11px;
    }
    @media (min-width: $desktop-width) {   
        width: auto;
        margin-left: 0;
    }   
}



// 
.history__range {    
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 35px;
    padding-bottom: 40px;   
    @media (min-width: $tablet-width) {
        display: flex;
        margin-top: 25px;  
        margin-left: auto;
        margin-right: auto;
        padding: 0 57px;  
        padding-bottom: 55px;
        
        width: 591px;
        justify-content:space-between;        
        align-items: center;
gap: 13px;
    }
    
}

.history__range__before,
.history__range__after {
    min-width: 49px;
    height: 30px;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 125% */
    text-transform: uppercase;
    background-color: inherit;
    border: none;
    @media (min-width: $tablet-width) {
        font-size: 20px;
        line-height: normal;
        padding-left: 10px;
        padding-top: 10px;
        min-width: 45px;
        height: 30px;
        font-size: 16px;
    }
}

.range--after
.history__toggler {
    justify-content: flex-end;
    
}

.range--before
.history__toggler {
    justify-content: flex-start;
    
}

.history__toggler {
    position: relative;
    padding: 4px 6px;
    display: flex;
    justify-content: center;
    border: 1px solid #cdcdcd;
    border-radius: 6px;
    width: 86px;
    height: 6px;
    background-color: #ffffff;
    border: none;
    
    @media (min-width: $tablet-width) {
        padding: 17px 17px;
        border-radius: 6px;
width: 426px;
margin-top: 13px;
margin-left: 13px;
    }
    
}




.range--before .history__toggler::after {
   position: absolute;
    content: "";
    width: 42px;
    height: 6px;
    background-color: #68B738;
    border-radius: 6px;
    right: 50%;
} 

.range--after .history__toggler::after {
    position: absolute;
     content: "";
     width: 42px;
     height: 6px;
     background-color: #68B738;
     border-radius: 6px;
     right: 3px;
    
 
 
 } 

.form-range {
    
    cursor: pointer;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: transparent;
    color: transparent;
    width: 85px;
      @media (min-width: $tablet-width) {
       min-width: 426px;
       margin-right: 13px; 
       border-radius: 6px;
       height: 6px;
       background: #565555;  
  }
  }

  .history__toggler input[type=range]::-webkit-slider-thumb {
    display: none; 
}
//   



.history__container-desktop-img {
    justify-content: center;
    
    min-height: 277px;
}

.history__img {
    position: relative;
    box-sizing: content-box;
    min-height: 232px;
    width: 280px;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    margin-bottom: 15px;

    outline: 5px solid red;
    outline-offset: -5px;
    @media (min-width: $tablet-width) {
        min-width: 591px;
    }
}


.history__images--fat,
.history__images--skinny {
    position: absolute;
    box-sizing: border-box;
    opacity: 0;
    transition: opacity 1s;
    margin-left: 0;
    margin-right: 0;    
    @media (min-width: $tablet-width) {
        display: none;        
    }
    @media (min-width: $desktop-width) {
        display: none; 
    }
}

.history__images--fat-skinny {
    
        display: none;         
    
    @media (min-width: $tablet-width) {
        display: inline;
        width: 592px;
        height: 511px;
    }
    @media (min-width: $desktop-width) {
        
    }
}

.history__images:first-child {
    opacity: 1;
  }

.history__spent__data {
    @media (min-width: $tablet-width) {
        align-content: flex-end;
    }
    @media (min-width: $desktop-width) {
        margin-left: 63px;
    }
}